import React, { Component } from "react";
import Chart from "react-google-charts";

class AreaChartBasic extends Component {
  render() {
    return (
      <Chart
        width="100%"
        height="300px"
        chartType="AreaChart"
        loader={<div>Loading Chart</div>}
        data={[
          ["Year", "Sales", "Expenses"],
          ["2013", 1000, 400],
          ["2014", 1170, 460],
          ["2015", 660, 1120],
          ["2016", 1030, 540]
        ]}
        options={{
          vAxis: {
            minValue: 0
          },
          colors: ["#7759de", "#2196f3"]
        }}
      />
    );
  }
}

export default AreaChartBasic;
